"use client";

import { useState, useEffect } from 'react';
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Form, FormControl, FormDescription, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { AIFieldCompletionInput, aiAssistedFieldCompletion } from '@/ai/flows/ai-assisted-field-completion';
import { Calendar } from "@/components/ui/calendar";
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover";
import { cn } from "@/lib/utils";
import { format } from "date-fns";
import { Exam } from '@/types';

const examSchema = z.object({
  mainExam: z.string().min(3, { message: "Main exam name must be at least 3 characters." }),
  examCode: z.string().min(3, { message: "Exam code must be at least 3 characters." }),
  subExams: z.array(z.string()).optional(),
  conductedBy: z.string().min(3, { message: "Conducting body must be at least 3 characters." }),
  examSector: z.string().min(1, { message: "Please select an exam sector." }),
  applicationPeriodStart: z.date(),
  applicationPeriodEnd: z.date(),
  status: z.enum(['active', 'draft', 'archived']).default('active'),
  version: z.number().min(1).default(1),
  effectiveDate: z.date(),
  eligibilityCriteria: z.array(z.string()).optional(),
  dataSourceLink: z.string().url({ message: "Invalid URL." }),
  internNote: z.string().min(10, { message: "Intern note must be at least 10 characters." }),
});

interface ExamFormProps {
    exam?: Exam;
    onClose: () => void;
}

const ExamForm: React.FC<ExamFormProps> = ({ exam, onClose }) => {
  const [isSubmitting, setIsSubmitting] = useState(false);

  const form = useForm<z.infer<typeof examSchema>>({
    resolver: zodResolver(examSchema),
    defaultValues: exam || {
      mainExam: '',
      examCode: '',
      subExams: [],
      conductedBy: '',
      examSector: '',
      applicationPeriodStart: new Date(),
      applicationPeriodEnd: new Date(),
      status: 'active',
      version: 1,
      effectiveDate: new Date(),
      eligibilityCriteria: [],
      dataSourceLink: '',
      internNote: '',
    },
  });

    useEffect(() => {
        if (exam) {
            form.reset(exam);
        }
    }, [exam, form]);

  const onSubmit = async (values: z.infer<typeof examSchema>) => {
    setIsSubmitting(true);
    // TODO: Implement submission logic
    await new Promise((resolve) => setTimeout(resolve, 1000));
    alert('Form submitted!');
    setIsSubmitting(false);
      onClose(); // Close after submitting
  };

  const { setValue } = form;

  const handleAIFieldCompletion = async () => {
    const examName = form.getValues('mainExam');
    const conductingBody = form.getValues('conductedBy');

    if (examName && conductingBody) {
      const input: AIFieldCompletionInput = { examName, conductingBody };
      try {
        const result = await aiAssistedFieldCompletion(input);
        setValue('examSector', result.category);
      } catch (error) {
        console.error("AI Completion Error:", error);
        alert("Error fetching AI suggestions.");
      }
    } else {
      alert("Please fill Exam Name and Conducting Body before using AI completion.");
    }
  };

  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4">
        <FormField
          control={form.control}
          name="mainExam"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Main Exam Name</FormLabel>
              <FormControl>
                <Input placeholder="e.g., UPSC Civil Services" {...field} />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />
        <FormField
          control={form.control}
          name="examCode"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Exam Code</FormLabel>
              <FormControl>
                <Input placeholder="e.g., UPSC_CSE_2025" {...field} />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />
        <FormField
          control={form.control}
          name="conductedBy"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Conducting Body</FormLabel>
              <FormControl>
                <Input placeholder="e.g., UPSC" {...field} />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />
        <div className="flex justify-end">
          <Button type="button" size="sm" onClick={handleAIFieldCompletion}>
            AI Assist
          </Button>
        </div>
        <FormField
          control={form.control}
          name="examSector"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Exam Sector</FormLabel>
              <Select onValueChange={field.onChange} defaultValue={field.value}>
                <FormControl>
                  <SelectTrigger>
                    <SelectValue placeholder="Select exam sector" />
                  </SelectTrigger>
                </FormControl>
                <SelectContent>
                  <SelectItem value="Defense">Defense</SelectItem>
                  <SelectItem value="Banking">Banking</SelectItem>
                  <SelectItem value="Civil Services">Civil Services</SelectItem>
                  <SelectItem value="Teaching">Teaching</SelectItem>
                  <SelectItem value="Medical">Medical</SelectItem>
                  <SelectItem value="Engineering">Engineering</SelectItem>
                  <SelectItem value="Other">Other</SelectItem>
                </SelectContent>
              </Select>
              <FormMessage />
            </FormItem>
          )}
        />
        <FormField
          control={form.control}
          name="applicationPeriodStart"
          render={({ field }) => (
            <FormItem className="flex flex-col space-y-3">
              <FormLabel>Application Start Date</FormLabel>
              <Popover>
                <PopoverTrigger asChild>
                  <FormControl>
                    <Button
                      variant={"outline"}
                      className={cn(
                        "w-[240px] pl-3 text-left font-normal",
                        !field.value && "text-muted-foreground"
                      )}
                    >
                      {field.value ? (
                        format(field.value, "PPP")
                      ) : (
                        <span>Pick a date</span>
                      )}
                    </Button>
                  </FormControl>
                </PopoverTrigger>
                <PopoverContent className="w-auto p-0" align="start">
                  <Calendar
                    mode="single"
                    selected={field.value}
                    onSelect={field.onChange}
                    disabled={(date) =>
                      date > new Date()
                    }
                    initialFocus
                  />
                </PopoverContent>
              </Popover>
              <FormMessage />
            </FormItem>
          )}
        />
        <FormField
          control={form.control}
          name="applicationPeriodEnd"
          render={({ field }) => (
           <FormItem className="flex flex-col space-y-3">
              <FormLabel>Application End Date</FormLabel>
              <Popover>
                <PopoverTrigger asChild>
                  <FormControl>
                    <Button
                      variant={"outline"}
                      className={cn(
                        "w-[240px] pl-3 text-left font-normal",
                        !field.value && "text-muted-foreground"
                      )}
                    >
                      {field.value ? (
                        format(field.value, "PPP")
                      ) : (
                        <span>Pick a date</span>
                      )}
                    </Button>
                  </FormControl>
                </PopoverTrigger>
                <PopoverContent className="w-auto p-0" align="start">
                  <Calendar
                    mode="single"
                    selected={field.value}
                    onSelect={field.onChange}
                    disabled={(date) =>
                      date > new Date()
                    }
                    initialFocus
                  />
                </PopoverContent>
              </Popover>
              <FormMessage />
            </FormItem>
          )}
        />
        <FormField
          control={form.control}
          name="effectiveDate"
          render={({ field }) => (
           <FormItem className="flex flex-col space-y-3">
              <FormLabel>Effective Date</FormLabel>
              <Popover>
                <PopoverTrigger asChild>
                  <FormControl>
                    <Button
                      variant={"outline"}
                      className={cn(
                        "w-[240px] pl-3 text-left font-normal",
                        !field.value && "text-muted-foreground"
                      )}
                    >
                      {field.value ? (
                        format(field.value, "PPP")
                      ) : (
                        <span>Pick a date</span>
                      )}
                    </Button>
                  </FormControl>
                </PopoverTrigger>
                <PopoverContent className="w-auto p-0" align="start">
                  <Calendar
                    mode="single"
                    selected={field.value}
                    onSelect={field.onChange}
                    disabled={(date) =>
                      date > new Date()
                    }
                    initialFocus
                  />
                </PopoverContent>
              </Popover>
              <FormMessage />
            </FormItem>
          )}
        />
        <FormField
          control={form.control}
          name="status"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Status</FormLabel>
              <Select onValueChange={field.onChange} defaultValue={field.value}>
                <FormControl>
                  <SelectTrigger>
                    <SelectValue placeholder="Select status" />
                  </SelectTrigger>
                </FormControl>
                <SelectContent>
                  <SelectItem value="active">Active</SelectItem>
                  <SelectItem value="draft">Draft</SelectItem>
                  <SelectItem value="archived">Archived</SelectItem>
                </SelectContent>
              </Select>
              <FormMessage />
            </FormItem>
          )}
        />
         <FormField
          control={form.control}
          name="version"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Version</FormLabel>
              <FormControl>
                <Input
                  type="number"
                  placeholder="e.g., 1"
                  {...field}
                  onChange={(e) => field.onChange(Number(e.target.value))}
                />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />
        <FormField
          control={form.control}
          name="dataSourceLink"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Data Source Link</FormLabel>
              <FormControl>
                <Input placeholder="e.g., https://example.com" {...field} />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />
        <FormField
          control={form.control}
          name="internNote"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Intern Note</FormLabel>
              <FormControl>
                <Textarea placeholder="e.g., Important points to remember" {...field} />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />
        <Button type="submit" disabled={isSubmitting}>
          {isSubmitting ? "Submitting..." : "Submit"}
        </Button>
          <Button type="button" variant="secondary" onClick={onClose}>
              Cancel
          </Button>
      </form>
    </Form>
  );
};

export default ExamForm;
